*{box-sizing:border-box;-webkit-box-sizing:border-box;padding:0;margin:0;border:0}*::after,*::before{box-sizing:border-box}:focus,:active{outline:none}a{color:inherit;text-decoration:none}a:visited{text-decoration:none}a:focus,a:active{outline:none}a:hover{text-decoration:none}nav,footer,header,aside{display:block}html,body{width:100%;font-size:16px;min-height:100vh;text-rendering:optimizeSpeed;line-height:1;-moz-text-size-adjust:100%;-webkit-text-size-adjust:100%}input,button,textarea{font-family:inherit;line-height:inherit;letter-spacing:inherit;font-size:inherit;font-weight:inherit;color:inherit;background-color:transparent;background-color:initial;border:none;text-transform:inherit}input::-moz-focus-inner,button::-moz-focus-inner,textarea::-moz-focus-inner{padding:0;border:0}button{cursor:pointer;color:inherit;background-color:transparent;background-color:initial}ul,ol{padding:0}ul,ol{list-style:none}img{max-width:100%;display:block}h1,h2,h3,h4,h5,h6{font-size:inherit;font-weight:inherit}input::-webkit-outer-spin-button,input::-webkit-inner-spin-button{-webkit-appearance:none;margin:0}input[type=number]{-moz-appearance:textfield}input,textarea,button,select,a{-webkit-tap-highlight-color:rgba(0,0,0,0)}